                            <div class="check__header-left-content">
                                <div class="check__header-content-item">
                                    <h6 class="check__header-content-title">Продавец:</h6>
                                    <h6 class="check__header-content-text">OUTLAS</h6>
                                </div>

                                <div class="check__header-content-item">
                                    <h6 class="check__header-content-title">Дата и время:</h6>
                                    <h6 class="check__header-content-text">{{ order_info.created_at | formatterDate }}</h6>
                                </div>

                                <div class="check__header-content-item">
                                    <h5 class="thankfulness">СПАСИБО ЗА ПОКУПКУ!</h5>
                                </div>
                            </div>

                        </div>

                        <div class="check__header-right">
                            <div class="check__table">
                                <table>
                                    <tbody style="width: 100%">

                                    <tr>
                                        <td>Имя покупателя:</td>
                                        <td class="check__table-text">
                                            {{ client.first_name }} {{ client.last_name }} {{ client.middle_name }}
                                        </td>
                                    </tr>

                                    <tr>
                                        <td>Адресс доставки:</td>
                                        <td class="check__table-text">
                                            {{ delivery_info.address }}
                                        </td>
                                    </tr>
                                    <tr>
                                        <td>Номер телефона:</td>
                                        <td class="check__table-text">+998{{ client.phone }}</td>
                                    </tr>
                                    <!--                                    <tr>-->
                                    <!--                                        <td>Доп. номер телефона:</td>-->
                                    <!--                                        <td class="check__table-text">+998939838393</td>-->
                                    <!--                                    </tr>-->
                                    <tr v-if="client.email">
                                        <td>Эл. почта</td>
                                        <td class="check__table-text">{{ client.email }}</td>
                                    </tr>
                                    <tr>
                                        <td>Тип оплаты:</td>
                                        <td class="check__table-text">{{ order_info.payment_type }}</td>
                                    </tr>
                                    </tbody>
                                </table>

                            </div>
                        </div>
                    </div>
                    <div class="check__body">
                        <div class="check__table check__table-main">
                            <table>
                                <thead>
                                <tr>
                                    <th class="Product__brand">Бренд</th>
                                    <th class="Product__description">Описание товара</th>
                                    <th class="Product__color">Цвет</th>
                                    <th>Размер</th>
                                    <th width="50" style="text-align: center">Скидка</th>
                                    <th>Кол-во</th>
                                    <th>Сумма</th>
                                </tr>
                                </thead>

                                <tbody>

                                <tr v-for="product in products" :key="product.id">
                                    <td class="Product__brand">{{ product.brand }}</td>
                                    <td class="Product__description">{{ product.description }}</td>
                                    <td class="Product__color">{{ product.color }}</td>
                                    <td>{{ product.variantValue }}</td>
                                    <td>{{ product.discount > 0 ? product.discount + '%' : '' }}</td>
                                    <td>{{ product.qty }} шт</td>
                                    <td>{{ product.price | money }} UZS</td>
                                </tr>


                                <tr>
                                    <td colspan="5"
                                        style="border-right: none;"
                                    >Стоимость доставки:
                                    </td>
                                    <td
                                        colspan="5"
                                        style="border-left: none;"
                                        class="bold-text"
                                        >{{ delivery_info.delivery_price }} UZS
                                    </td>
                                </tr>
                                <tr>
                                    <td colspan="5"
                                        style="border-right: none;"
                                    >ОБЩАЯ СУММА:
                                    </td>
                                    <td colspan="5"
                                        style="border-left: none;"
                                        class="bold-text"
                                    >{{ order_info.total_price | money }} UZS
                                    </td>
                                </tr>

                                </tbody>
                            </table>
                        </div>
                    </div>

