</template>

<script>
export default {
    props: ['id'],
    data() {
        return {
            courier: null,
            client: null,
            delivery_info: null,
            order_info: null,
            products: [],
        }
    },
    methods: {
        getCheckInfo() {
            this.$store.commit('showLoading');
            this.$http.get(`orders/${this.id}`)
                .then(res => {
                    this.order_info = res.data.data.order_info;
                    this.delivery_info = res.data.data.delivery_info;
                    this.courier = res.data.data.courier;
                    this.client = res.data.data.client;
                    this.products = res.data.data.products;
                    this.$nextTick(() => {
                      setTimeout(() => {
                        window.print();
                      }, 1000)
                    })
                })
                .finally(() => this.$store.commit('clearLoading'))
        }
    },
    mounted() {
        this.getCheckInfo();
    }
}
</script>

